<template>
   <v-layout row wrap>
      <v-flex sm12>
         <v-data-table :headers="headers" :items="symbols" hide-actions>
            <template slot="headerCell" slot-scope="{ header }">
               <span class="subheading font-weight-light text-success text--darken-3 cn">
                  {{ header.text }}
               </span>
            </template>
            <template slot="items" slot-scope="props">
               <td class="cn">{{ props.item.symbol }}</td>
               <td>{{ props.item.weight }}</td>
               <td>{{ props.item.price }}</td>
               <td v-text="getRatio(props.item)"></td>
            </template>
         </v-data-table>
      </v-flex>
      <v-flex sm12 class="cn">
         <h2 class="font-weight-light mb-4"> <span class="cn"></span></h2>
         <h5 class="cn">
            <span class="cn"></span> 
         </h5>
         <ul>
            <li>
               ,49, (/) x (/) 
            </li>
            <li>
               , 0.0632 0.0632
            </li>
            <li>
               (Tick),()
            </li>
            <li>
                10:30:00 ~ 10:31:00
            </li>
            <li>
                = .  = . 
            </li>
         </ul>
         <h5>
            <span class="cn"></span>
         </h5>
         <ul>
            <li>
               1  60
            </li>
            <li>
                = () - 
            </li>
            <li>
               (n)
            </li>
         </ul>
         <h5>
            <span class="cn"></span>
         </h5>
         <ul>
            <li>
               1 ,   -1
            </li>
         </ul>
         
         
      </v-flex>
      <v-flex sm6 class="cn">
         <v-img :aspect-ratio="548/169"  class="white--text" src="https://n3s5qg.ch.files.1drv.com/y4mj0hsZC2a_ibUXzboF_pJcndsACBdw7go1g-2AmXNsSk1QHXMun5JtmJ-9oRS1AukwYgxxfVYA77YD2aMySF4d17PbCGcC-IhLPgftXuJPo-rj-8jqK70JXNcgQ3jfhT0CybkWaR7VGQWvatVcPc8eqOqQcEeUp3JfhGxRqL-dQMWcew_kRrj12U-mlFiXEJE">
         <v-container fill-height fluid>
            <v-layout fill-height>
               <v-flex xs12 align-end flexbox>
                  <div class="headline ">
                     <span class="cn"> 1-1</span>
                  </div>
                  
               </v-flex>
            </v-layout>
         </v-container>
      </v-img>
      </v-flex>
      <v-flex sm6 class="cn">
      </v-flex>
   </v-layout>
</template>

<script>
export default {
   data () {
		return {
			headers: [
				{
					sortable: false,
					text: '',
					value: 'symbol'
            },
            {
					sortable: false,
					text: ' (%)',
					value: 'weight'
				},
				{
					sortable: false,
					text: '',
					value: 'price'
            },
            {
					sortable: false,
					text: '',
					value: 'ratio'
            }
         ],
         symbols: [{
            symbol: '',
            weight: '21.1929',
            price: 221,
            ratio: 1
         },{
            symbol: '',
            weight: '4.2131',
            price: 70.3,
            ratio: 0
         },{
            symbol: '',
            weight: '3.7738',
            price: 111,
            ratio: 0
         }]
			
		}
   },
   methods: {
		getRatio(item){
         if(item.ratio) return item.ratio;
         let baseStock = this.symbols[0];
         
         let ratio = (item.price / baseStock.price) * (item.weight / baseStock.weight);
         return Math.round(ratio * 10000)/ 10000;
      }
	}
}
</script>
