<template>
   <v-layout row wrap>
      <v-flex sm12>
         <v-data-table :headers="headers" :items="prices" hide-actions>
            <template slot="headerCell" slot-scope="{ header }">
               <span class="subheading font-weight-light text-success text--darken-3 cn">
                  {{ header.text }}
               </span>
            </template>
            <template slot="items" slot-scope="props">
               <td class="cn">{{ props.item.symbol }}</td>
               <td>{{ props.item.price }}</td>
               <td>{{ props.item.qty }}</td>
            </template>
         </v-data-table>
      </v-flex>
      <v-flex sm12 class="cn">
         <h2 class="font-weight-light mb-4"> <span class="cn"></span></h2>
         <h5 class="cn">
            <span class="cn"></span> 
         </h5>
         <ul>
            <li>
               
            </li>
            <li>
               
               <span v-for="(item, index) in prices" :key="index">
                  ( {{ item.price }} x {{ item.qty }} ) {{ index === prices.length - 1 ? '' : '+' }}
               </span>
               / (
               <span v-for="(item, index) in prices" :key="0 - index - 1">
                  {{ item.qty }} {{ index === prices.length - 1 ? '' : '+' }}
               </span>
               ) = {{ result }}
            </li>
            
         </ul>
         <h5>
            <span class="cn"></span>
         </h5>
         <ul>
            <li>
               1  60
            </li>
            <li>
                =  - (n) (9806.3098103.7)
               
            </li>
         </ul>
         <h5>
            <span class="cn"></span>
         </h5>
         <ul>
            <li>
               11 ,   1-1
            </li>
         </ul>
         
         
      </v-flex>
      <v-flex sm6 class="cn">
         <v-img :aspect-ratio="443/101"  class="white--text" src="https://nns5qg.ch.files.1drv.com/y4mDMhubkJ07zT910u4R3tzylpxqXmnpyL9II_5OaOGeh7HBJRKbkqYzbWxnz7qaePOkm7-UzHI1LMJZjqpByc4Pf7caouObKbQBcPfUmotBUesETBZ9LLBas__3GCcln4Hbq0mmWg3N0d2QmARpzhKygyBqyoloJdOfXrB_KQ_0UDqeGrKoPMejtwylyLjou8U">
         <v-container fill-height fluid>
            <v-layout fill-height>
               <v-flex xs12 align-end flexbox>
                  <div class="headline ">
                     <span class="cn"> 1-1</span>
                  </div>
                  
               </v-flex>
            </v-layout>
         </v-container>
      </v-img>
      </v-flex>
      <v-flex sm6 class="cn">
      </v-flex>
   </v-layout>
</template>

<script>
export default {
   data () {
		return {
			headers: [
				{
					sortable: false,
					text: '',
					value: 'symbol'
				},
				{
					sortable: false,
					text: '',
					value: 'price'
            },
            {
					sortable: false,
					text: '',
					value: 'qty'
				}
         ],
         prices: [{
            symbol: '',
            price: 9800,
            qty: 300
         },{
            symbol: '',
            price: 9805,
            qty: 100
         },{
            symbol: '',
            price: 9810,
            qty: 200
         }]
			
		}
   },
   computed: {
      result(){
         let sum = 0;
         let total = 0;
         for(let i = 0; i < this.prices.length; i++){
            sum += (this.prices[i].price * this.prices[i].qty);
            total += this.prices[i].qty;            
         }
         let val = sum / total;
         return Math.round(val * 100) / 100;
      }
   }
}
</script>
